.menu {
  font-weight: 700;
  background-color: #c2c2c2;

  box-sizing: border-box;
  height: 100vh;
  padding: 29px 0;

  @include onTab {
    padding: 36px 0;
  }

  overflow-y: auto;

  &__top {
    @include top-actions;

    margin-bottom: 44px;

    @include onTab {
      margin-bottom: 48px;
    }
  }

  &__list {
    list-style: none;
    margin-bottom: 32px;
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    display: inline-block;
    padding-bottom: 7px;
    border-bottom: 1px solid transparent;

    font-size: 22px;
    line-height: 22px;
    text-decoration: none;
    letter-spacing: 2px;
    text-transform: uppercase;
    color: $main-color;

    transition: border-color 0.3s;

    &:hover {
      border-color: $main-color;
    }
  }

  &__call {
    display: block;
    width: max-content;
    text-decoration: none;
    color: $main-color;

    &--phone {
      font-size: 16px;
      line-height: 27px;

      margin-bottom: 16px;
    }

    &--action {
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 2px;
      text-transform: uppercase;

      padding-bottom: 8px;

      border-bottom: 1px solid $main-color;
    }
  }
}
